import React, { Suspense, useEffect, useRef, lazy } from "react";
import { themeImpressionTracking } from '../../../../utils'
import { isTagEmbed, isTagShop, isTagbox } from "../../../../actions/api";
const Media =  lazy(() => import("./Media"));
const Network =  lazy(() => import("../../Elements/Network/Network"));
const ShopButton =  lazy(() => import("../../Elements/ShopButton/ShopButton"));
const CTAButton =  lazy(() => import("../../Elements/CTA/CtaButton"));
const Card = ({ itemData, personalization, ThemeRule, wallID, ownerId, onClickToCTA, clickToShowPopUp, itemIndex, adjustWidth, wall, onClickPopUpSlider }) => {
  const isMerge = personalization.widgetTheme == 81 ? false : true

  const postTrack = useRef(null);
  const themeMode = (ThemeRule.postHover == 0 && isTagShop) ? 'light' : 'dark'
  const overlay = isTagbox ? 'tb_ht_color_overlay' : 'tb_ht_post_overlay-' + themeMode
  const overlayStyle = { backgroundColor: isTagbox ? ThemeRule.cardColor : '' }
  const cardSize = { padding: personalization.padding / 2 };
  const mediaType = itemData.type !== 1 ? true : false;
  const ctaStatus = (itemData.cta && Object.keys(itemData.cta).length > 0 && itemData.cta.status) ? isTagEmbed ? true : !(isMerge && isTagbox) ? true : false : false;
  const cardRadius = (isMerge && isTagbox) ? '50%' : null;
  const isShopBtn = isTagShop && ThemeRule.shoppingText != '' && itemData.ugc_products.length > 0 ? true : false;

  const isProduct = isTagShop && itemData.ugc_products && itemData.ugc_products.length > 0 ? true : false
  const isShopIcon = isTagShop && (isProduct && itemData.hotspot === 0) ? true : false
  const isHotspot = isTagShop && itemData.hotspot && isProduct ? true : false

  useEffect(() => {
    if (isTagShop) themeImpressionTracking(postTrack);
  }, []);
  useEffect(() => {
    if (itemData.isPopUp) onClickPopUpSlider(itemIndex, itemData)
  }, [itemIndex, itemData.isPopUp, itemData]);
  return <div id={`tb-ht-post-${itemData.id}`} className="tb_ht_post_wrapper" style={cardSize} tb-network={itemData.network.id} tb-product={isShopIcon ? 1 : isHotspot ? 2 : 0} ref={postTrack} post-id={itemData.referenceId ? itemData.referenceId : itemData.id} feed-id={itemData.feedId} area-label={itemData.content} role="article">
    <div className="tb_ht_post_in" onClick={clickToShowPopUp(itemIndex, itemData)} style={{ borderRadius: cardRadius }} role="button" aria-roledescription="Click to open pop up" tabIndex={"0"}>
      <div className="tb_ht_post_media_wrapp" role="button" tabIndex="0" aria-label="Pop Up Button">
        {mediaType ? <Suspense fallback={<></>}><Media itemData={itemData} wallID={wallID} themeID={personalization.widgetTheme} ownerId={ownerId} ThemeRule={ThemeRule} wall={wall} /></Suspense> : ''}
        <div className="tb_ht_post_details">
          <div className="tb_ht_social_">
          <Suspense fallback={<></>}><Network networkClass={'tb_ht_social_ico_'} network={itemData.network} ThemeRule={ThemeRule} isDefault={ThemeRule.iconType === 1 ? true : false} Personalization={personalization} /></Suspense>
          </div>
          {isShopBtn ? <Suspense fallback={<></>}><ShopButton ThemeRule={ThemeRule} btnClass={'tb_ht_btn_wrap'} /></Suspense> : null}
          {ctaStatus ? <div className="tb_ht_post_cta" onClick={(e) => { e.stopPropagation() }}><Suspense fallback={<></>}><CTAButton ctaClass={'tb_ht_post_cta_btn'} cta={itemData.cta} item={itemData} onClickToCTA={onClickToCTA} /></Suspense> </div> : ''}
        </div>
        <div className={`tb_ht_post_overlay ${overlay}`} style={overlayStyle}><div></div></div>
      </div>
    </div>
  </div>
}
export default Card 