import React, { Component, memo } from "react";
import { isTagEmbed } from "../../../actions/api";
import { connect } from "react-redux";
import { Splide, SplideSlide, SplideTrack } from '@splidejs/react-splide';
import { getDataNextSteps } from '../../../actions/themeActions'
import { Grid } from '@splidejs/splide-extension-grid';
import Card from "./Elements/Card";
import "./scss/HighlightTheme.scss";
import '../scss/ThemesSplide.scss'
import { isTagShop } from '../../../actions/api'
import { IS_THEME } from "../../../utils";

class HighlightTheme extends Component {
  constructor(props) {
    super(props)
    // this.secondaryRef = React.createRef();
    this.inputRefs = {};
  }
  requestData = () => {
    const { appendData, wall, preRender, hasMoreData, getDataNextSteps } = this.props;
    if (hasMoreData) {
      getDataNextSteps(
        wall.Wall.id,
        Math.floor(Date.now() / 1000),
        wall.ThemeRule.numberOfPosts,
        appendData.networkID,
        appendData.after,
        preRender,
        appendData.heightEvent
      );
    }
  };
  componentDidMount() {
    setTimeout(() => {
      const classNameRole = document.querySelectorAll('.splide__slide');
      classNameRole.forEach((element, index) => {
        element.removeAttribute("role");
      });
    }, 100);
  }
  componentDidUpdate() {
    const value = Object.values(this.inputRefs)[0];
    const { postData, completeDataObject, onClickPopUpSlider, wall, parentID } = this.props;
    const rowCount = wall.ThemeRule.multiRow;
    if (value && Object.values(value)[5].length > 0) {
      let data = Object.values(value)[5]
      data.map((item, itemIndex) => {
        if (item.classList) {
          const filterID = String(item.classList.value).split(" ").filter((fCheck) => String(fCheck).includes("tb_"))
          var getID = filterID[0]
          if (String(filterID[0]).includes("tb_")) {
            getID = String(filterID[0]).replace("tb_", "")
          }
          const findIndex = postData.findIndex((find) => find == getID)
          let clickD = document.querySelectorAll(`#${item.id}`)
          for (let index = 0; index < clickD.length; index++) {
            let clickDD = clickD[index];
            let p_id = clickDD.getAttribute('p_id')
            if (String(parentID) === String(p_id)) {
              clickDD.addEventListener('click', event => {
                event.preventDefault();
                const itemData = completeDataObject[getID]
                onClickPopUpSlider(findIndex, itemData)
              });
            }
          }
        }
      })
    }
    else if (rowCount == 2) {
      let data = Object.values(value)[5]
      data.map((item, itemIndex) => {
        if (item && item.childNodes.length > 0) {
          item.childNodes.forEach((childItem) => {
            if (childItem && childItem.childNodes.length > 0) {
              childItem.childNodes.forEach((subChildItem) => {
                if (subChildItem.classList) {
                  const filterID = String(subChildItem.classList.value).split(" ").filter((fCheck) => String(fCheck).includes("tb_"))
                  var getID = filterID[0]
                  if (String(filterID[0]).includes("tb_")) {
                    getID = String(filterID[0]).replace("tb_", "")
                  }
                  const findIndex = postData.findIndex((find) => find == getID)
                  // console.log("subChildItem.id", subChildItem);
                  let clickD = document.querySelectorAll(`.tb_hs_post_container #${subChildItem.id}`)
                  // console.log('clickD:', clickD);
                  for (let index = 0; index < clickD.length; index++) {
                    let clickDD = clickD[index];
                    let p_id = clickDD.getAttribute('p_id')
                    // console.log('p_id:,parentID ', p_id, parentID);
                    if (String(parentID) === String(p_id)) {
                      clickDD.addEventListener('click', event => {
                        event.preventDefault();
                        // console.log("from__clickDD==> parentID, clickD", parentID, clickDD)
                        const itemData = completeDataObject[getID]
                        // console.log('findIndex, itemData', findIndex, itemData);
                        onClickPopUpSlider(findIndex, itemData)
                      });
                    }
                  }
                }
              })
            }
          });


        }

      })
    }
  }
  onUpdateData = event => {

  }

  autoScrollSlider = (splide, prev, next, destIndex) => {
    const { wall } = this.props;
    const isTagembedStatus = isTagEmbed || wall?.User?.userDatabaseType == "tagembed"
    const currentIndex = isTagembedStatus ? (splide["_o"].perPage + splide.index) : splide.index
    const slideLength = splide.length
    const permove = isTagembedStatus ? 1 : splide["_o"].perPage
    if (slideLength - currentIndex === permove) this.requestData()
  }
  render() {
    const { postData, completeDataObject, wall, clickToShowPopUp, onClickToCTA, parentID, adjustWidth, cardNumber, onClickPopUpSlider } = this.props;

    const isTagembedStatus = isTagEmbed || wall?.User?.userDatabaseType == "tagembed"
    const inputName = parentID;
    const numberOfCoumn = wall.ThemeRule.numberOfCoumn;
    const mobileColumn = wall.ThemeRule.mobileColumn;
    const slidePost = wall.ThemeRule.slidePost;
    const slideDuration = wall.ThemeRule.slideDuration;
    const autoScrollStatus = wall.Personalization.autoScrollStatus;
    const rowCount = isTagembedStatus ? wall.ThemeRule.multiRow : 1;
    const cloneStatus = isTagembedStatus && wall.Personalization?.clonePost !== undefined ? wall.Personalization.clonePost === 0 ? { clones: 0 } : {} : {};
    const isType = autoScrollStatus != 1 ? Object.keys(cloneStatus).length > 0 ? false : (isTagembedStatus ? 'slide' : 'slide') : (isTagembedStatus ? 'slide' : 'slide')
    const settings = {
      ...cloneStatus,
      grid: {
        rows: rowCount,
      },
      type: autoScrollStatus === 1 ? false : isType,
      rewind: isTagembedStatus ? true : true,
      rewindSpeed: 1000,
      interval: slidePost === 1 && parseInt(slideDuration) > 0 ? (parseInt(slideDuration) * 1000) : 5000,
      perPage: isTagShop ? cardNumber : numberOfCoumn > 0 ? numberOfCoumn : 5,
      perMove: 1,
      gap: 0,
      autoplay: slidePost === 1 ? true : false,
      padding: 0,
      pagination: false,
      arrows: true,
      breakpoints: {
        560: {
          perPage: isTagShop ? cardNumber : mobileColumn > 0 ? mobileColumn : 1,
        },
        767: {
          perPage: isTagShop ? cardNumber : mobileColumn > 0 ? mobileColumn : 2,
        },
        991: {
          perPage: isTagShop ? cardNumber : mobileColumn > 0 ? mobileColumn : 3,
        },
        1200: {
          perPage: isTagShop ? cardNumber : numberOfCoumn > 0 ? numberOfCoumn : 4,
        },
        1400: {
          perPage: isTagShop ? cardNumber : numberOfCoumn > 0 ? numberOfCoumn : 5,
        },
        1600: {
          perPage: isTagShop ? cardNumber : numberOfCoumn > 0 ? numberOfCoumn : 6,
        }
      }
    };
    return (parentID != "" ?
      <div className="tb_ht_post_container">
        <Splide hasTrack={false} className="tb_ht_post_slider" options={settings} extensions={{ Grid }} ref={(parentID) => this.inputRefs[inputName] = parentID} onMove={this.autoScrollSlider}>
          <SplideTrack>
            {

              (wall.ThemeRule.multiRow > 1 && postData.length !== 1 && postData.length % 2 !== 0 ? postData.pop() : postData) && ((postData && postData.length > 0)) && postData.map((item, index) => {
                const cardData = completeDataObject[item];

                return <SplideSlide key={index} style={{ margin: 0, padding: 0 }} className={`tb_${cardData.id}`} p_id={parentID} >
                  <Card itemData={cardData} itemIndex={index} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} clickToShowPopUp={clickToShowPopUp} wallID={wall.Wall.id} onClickToCTA={onClickToCTA} adjustWidth={adjustWidth} wall={wall} onClickPopUpSlider={onClickPopUpSlider} />
                </SplideSlide>
              })}
          </SplideTrack>

          <div className="splide__arrows splide__arrows--ltr tb_ht_arrow_wrapper_">
            <div className="tb_ht_arrow splide__arrow splide__arrow--prev tb_ht_arrow tb_ht_arrow_left__ tb__icon tb-arrow-left-alt" role="button" aria-label="Go to previous slide"><div></div></div>
            <div className="tb_ht_arrow splide__arrow splide__arrow--next  tb_ht_arrow_right__ tb__icon tb-arrow-right-alt" role="button" aria-label="Go to next slide"><div></div></div>

          </div>
        </Splide>
      </div> : null
    );
  }
}

const mapStateToProps = state => {
  return {

  }
}

const mapDispatchToProps = dispatch => {
  return {
    getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow)),
  }
}
export default memo(connect(mapStateToProps, mapDispatchToProps)(HighlightTheme));
